.console-div {
  display: flex;
  position: absolute;
  top: 0;
  padding: 20px;
  background-color: rgb(231, 231, 231);
  border-radius: 0 0 8px 8px;
  color: rgb(46, 46, 46);
}

.adjust-btns {
  display: flex;
}

button {
  margin: 4px;
  border: none;
  background-color: rgb(231, 231, 231);
  font-size: 20px;
  cursor: pointer;
}

.session, .resting{
    padding-right: 14px;
    padding-left: 14px;
}

.resting::after{
    left: 15%;
    position: absolute;
    content: "Break Length";
    top: 0;
    text-align: center;
}

.session::after{
    left: 60%;
    position: absolute;
    content: "Session Length";
    top: 0;
    text-align: center;
}

.more-info{
  font-size: 30px;
}

.repo{
  top: 1%;
  right: 35%;
}

.wikipedia{
  top: 1%;
  left: 35%;
}

.break-increment{
  margin-left: 1px;

}
.session-increment{
  margin-left: 1px;
}

.break-decrement, .break-increment, .session-decrement, .session-increment {
  margin-bottom: 15px;
}